import React, { useEffect, useState } from "react";
import { Header} from "./Header";
import api from "../utils/Api";
import { EditProfilePopup } from "./EditProfilePopup";
import { EditAvatarPopup } from "./EditAvatarPopup";
import { AddPlacePopup } from "./AddPlacePopup";
import { DeletePlacePopup } from "./DeletePlacePopup";
import { Main } from "./Main";
import { Footer } from "./Footer";
import { ImagePopup } from "./ImagePopup";
import { CurrentUserContext } from "../context/CurrentUserContext";

function App() {
  const [currentUser, setCurrentUser] = useState({});
  const [cards, setCards] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isStartLoading, setStartIsLoading] = useState(false);
  const [deletingCard, setDeletingCard] = useState("");

  useEffect(() => {
    setStartIsLoading(true)
    Promise.all([api.getUserInfo(), api.getInitialCards()])
      .then((response) => {
        const [profile, cards] = response;
        //fill profile elemements
        setCurrentUser(profile);
        //fill cards elemements
        const items = cards.map((item) => ({
          key: item._id,
          cardId: item._id,
          ownerId: item.owner._id,
          likes: item.likes.length,
          like: item.likes,
          name: item.name,
          src: item.link,
        }));
        setCards(items);
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setStartIsLoading(false);
      });
  }, []);

  function handleCardLike(card) {
    // –°–Ω–æ–≤–∞ –ø—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ –ª–∞–π–∫ –Ω–∞ —ç—Ç–æ–π –∫–∞—Ä—Ç–æ—á–∫–µ
    const cardId = card.cardId;
    const isLiked = card.like.some((i) => i._id === currentUser._id);

    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –∑–∞–ø—Ä–æ—Å –≤ API –∏ –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –∫–∞—Ä—Ç–æ—á–∫–∏
    api
      .changeLikeCardStatus(cardId, isLiked)
      .then((newCard) => {
        const newCardFormat = {
          key: newCard._id,
          cardId: newCard._id,
          ownerId: newCard.owner._id,
          likes: newCard.likes.length,
          like: newCard.likes,
          name: newCard.name,
          src: newCard.link,
        };
        // –§–æ—Ä–º–∏—Ä—É–µ–º –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞ –æ—Å–Ω–æ–≤–µ –∏–º–µ—é—â–µ–≥–æ—Å—è, –ø–æ–¥—Å—Ç–∞–≤–ª—è—è –≤ –Ω–µ–≥–æ –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
        const newCards = cards.map((c) =>
          c.cardId === cardId ? newCardFormat : c
        );
        // –û–±–Ω–æ–≤–ª—è–µ–º —Å—Ç–µ–π—Ç
        handleUpdateCard({ newCards });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  function handleCardDelete(card) {
    setIsLoading(true);
    const withoutDeleted = cards.filter((—Å) => card !== —Å.cardId);
    api
      .deleteCard(card)
      .then(() => {
        setCards(withoutDeleted);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  function handleUpdateCard(newCards) {
    const updatedCards = newCards.newCards;
    const items = updatedCards.map((item) => ({
      key: item.key,
      cardId: item.cardId,
      ownerId: item.ownerId,
      likes: item.likes,
      like: item.like,
      name: item.name,
      src: item.src,
    }));
    setCards(items);
  }

  function handleUpdateUser(newProfile) {
    setIsLoading(true);
    api
      .setUserInfo(newProfile.name, newProfile.about)
      .then((response) => {
        setCurrentUser(response);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  function handleUpdateAvatar(avatarLink) {
    setIsLoading(true);
    api
      .setAvatar(avatarLink.avatar)
      .then((response) => {
        setCurrentUser(response);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  function handleAddPlaceSubmit(newPlace) {
    setIsLoading(true);
    api
      .insertCard(newPlace.place, newPlace.link)
      .then((newCard) => {
        const newCardFormat = {
          key: newCard._id,
          cardId: newCard._id,
          ownerId: newCard.owner._id,
          likes: newCard.likes.length,
          like: newCard.likes,
          name: newCard.name,
          src: newCard.link,
        };
        setCards([newCardFormat, ...cards]);
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = useState(false);
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = useState(false);
  const [isPlacePopupOpen, setIsPlacePopupOpen] = useState(false);
  const [isDeletePlacePopupOpen, setIsDeletePlacePopupOpen] = useState(false);
  const [selectedCard, setSelectedCard] = useState({});

  const closeAllPopups = () => {
    setIsEditProfilePopupOpen(false);
    setIsEditAvatarPopupOpen(false);
    setIsAddPlacePopupOpen(false);
    setIsPlacePopupOpen(false);
    setIsDeletePlacePopupOpen(false);
    setSelectedCard({});
  };

  const handleCardClick = (props) => {
    setIsPlacePopupOpen(true);
    setSelectedCard(props);
  };

  const handleEditProfileClick = () => {
    setIsEditProfilePopupOpen(true);
  };

  const handleEditAvatarClick = () => {
    setIsEditAvatarPopupOpen(true);
  };
  const handleAddPlaceClick = () => {
    setIsAddPlacePopupOpen(true);
  };

  const handleDeletePlaceClick = (props) => {
    setDeletingCard(props.cardId);
    setIsDeletePlacePopupOpen(true);
  };

  return (
    <CurrentUserContext.Provider value={currentUser}>
      <div className="page">
        <Header />
        {/* handlers of the page buttons */}
        <Main
          cards={cards}
          onEditAvatar={handleEditAvatarClick}
          onEditProfile={handleEditProfileClick}
          onAddPlace={handleAddPlaceClick}
          onCardClick={handleCardClick}
          onCardDelete={handleDeletePlaceClick}
          onCardLike={handleCardLike}
          isStartLoading={isStartLoading}
        />

        {/* PopUp for PROFILE Editing */}
        <EditProfilePopup
          isOpen={isEditProfilePopupOpen}
          onClose={closeAllPopups}
          onUpdateUser={handleUpdateUser}
          isLoading={isLoading}
        />

        {/* PopUp for AVATAR Editing */}
        <EditAvatarPopup
          isOpen={isEditAvatarPopupOpen}
          onClose={closeAllPopups}
          onUpdateAvatar={handleUpdateAvatar}
          isLoading={isLoading}
        />

        {/* PopUp for CARD Adding */}
        <AddPlacePopup
          isOpen={isAddPlacePopupOpen}
          onClose={closeAllPopups}
          onAddPlace={handleAddPlaceSubmit}
          isLoading={isLoading}
        />

        {/* PopUp for CARD Deleting */}
        <DeletePlacePopup
          isOpen={isDeletePlacePopupOpen}
          onClose={closeAllPopups}
          isLoading={isLoading}
          onDeleteCard={handleCardDelete}
          deletingCard={deletingCard}
        />

        <ImagePopup
          isOpen={isPlacePopupOpen}
          onClose={closeAllPopups}
          name={selectedCard.name}
          src={selectedCard.src}
        />
        <Footer />
      </div>
    </CurrentUserContext.Provider>
  );
}

export default App;
