import React, { useCallback } from "react";
import { PopupWithForm } from "./PopupWithForm";

export const EditAvatarPopup = (props) => {
  const linkRef = React.useRef("");

  const handleSubmit = useCallback((e) => {
    e.preventDefault();
    props.onUpdateAvatar({
      avatar: linkRef.current.value,
    });
  }, [props]);

  React.useEffect(() => {
    linkRef.current.value = "";
  }, [props.isOpen]);

  return (
    <PopupWithForm
      isOpen={props.isOpen}
      onClose={props.onClose}
      onSubmit={handleSubmit}
      name={"editAvatarForm"}
      title={"–û–±–Ω–æ–≤–∏—Ç—å –∞–≤–∞—Ç–∞—Ä"}
      isLoading={props.isLoading}
      buttonText={"–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"}
      buttonTextIsLoading={"–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ ..."}
    >
      {
        <div className="popup__field popup__field_info_other">
          <input
            type="url"
            placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
            className="popup__text"
            name="link"
            id="link"
            required
            ref={linkRef}
          />
          <span id="link-error" className="popup__input"></span>
        </div>
      }
    </PopupWithForm>
  );
};

export default EditAvatarPopup;
